	import uniPopup from '@/components/uni-popup/uni-popup.vue'
    import uniIcon from "@/components/uni-icon/uni-icon.vue"
	import cmdIcon from "@/components/cmd-icon/cmd-icon.vue"
	import wlpNumberShopping from "../../components/wlp-number-shopping/wlp-number-shopping.vue"
	import calendar from '../../components/date-picker/date-picker.vue'
	import uniCountdown from "@/components/uni-countdown/uni-countdown.vue"
	import uniTag from "@/components/uni-tag/uni-tag.vue"
	export default {
		components: {
			uniPopup,
			uniIcon,
			cmdIcon,
			wlpNumberShopping,
			calendar,
			uniCountdown,
			uniTag
		},
		props:{
			showHeader1:{
				type:Boolean,
				default:false
			},
			showHeader2:{
				type:Boolean,
				default:false
			},
			showCountDown:{
				type:Boolean,
				default:false
			},
			showDate1:{
				type:Boolean,
				default:false
			},
			showDate2:{
				type:Boolean,
				default:false
			},
			showDate3:{
				type:Boolean,
				default:false
			},
			showDate4:{
				type:Boolean,
				default:false
			},
			showMessage1:{
				type:Boolean,
				default:false
			},
			showMessage2:{
				type:Boolean,
				default:false
			},
			showMessage3:{
				type:Boolean,
				default:false
			},
			showMessage4:{
				type:Boolean,
				default:false
			},
			showMessage5:{
				type:Boolean,
				default:false
			},
			showRule1:{
				type:Boolean,
				default:false
			},
			showRule2:{
				type:Boolean,
				default:false
			},
			showBottom1:{
				type:Boolean,
				default:false
			},
			showBottom2:{
				type:Boolean,
				default:false
			},
			url:{
				type:String,
				default:""
			},
			
			dateLeft:{
				type:String,
				default:""
			},
			dateMid:{
				type:String,
				default:""
			},
			messageContent:{
				type:String,
				default:"支付成功后不可退，如未兑换将扣除全额费用"
			},
			goodsMag:{
				type:String,
				default:"请在出行的前一日18：00前预定"
			},
			orderMessage:{
				type:String,
				default:"免费预约"
			}
		},
		data() {
			return {
				goodsName:"桃花源禅意民宿商户",
				goodsPicture:"/static/orderPicture/1.6.jpg",
				goodsType:"单人间",
				goodsPrice:180,
				phoneNumber:"15029092646",
				moneys:"￥1080",
				type: '',
				time:'18:00',
				beginDate: '',
				beginDateWeek:'',
				endDate: '',
				endDateWeek:'',
				showCaledar: false,
				dateSum:0,
				address:"陕西省西安市雁塔区高新四路和科技路交叉口处 高新九号广场10号楼",
			    Poplist:[
			    	"14:00","15:00","16:00","17:00",
			    	"18:00","19:00","20:00","21:00",
			    	"22:00","23:00","24:00","次日01:00",
			    	"次日02:00","次日03:00","次日04:00","次日05:00",
			    	"次日06:00"
			    ],
				stayTime:[],
				num:2,
				deliveryType:"快递运输",
			    deliveryDate:"工作日与周末都可收货",
				tag:[
					{
						tagId:0,
						tagText:"快速配送",
						tagType:"success"
					},
					{
						tagId:1,
						tagText:"上门自提",
						tagType:"default"
					},
				],
				tagNext:[
					{
						tagId:0,
						tagText:"工作日与周末都可收货",
						tagType:"default"
					},
					{
						tagId:1,
						tagText:"只可工作日收货",
						tagType:"default"
					},
					{
						tagId:2,
						tagText:"只可周末收货",
						tagType:"success"
					},
				],
				
			}
		},
		onBackPress() {
			if (this.showCaledar !== false) {
				this.showCaledar = false;
				return true;
			}
		},
		onLoad() {
			this.init();
		},
		created() {
			this.init();
		},
		methods: {
			init(){
				    let date = new Date();
					let year = date.getFullYear(),month = date.getMonth()+1,day = date.getDate();
					let today = year+"-"+month+"-"+day;
					let dayCount = new Date(year,month,0);
					if(day == dayCount){
						month = month + 1;
						day = 0;
					}
					let tomorrow = year+"-"+month+"-"+(day+1);
					let aftertomorrow = year+"-"+month+"-"+(day+2);
					let todayDate=[
						{
							date:today,
							money:this.num+"*"+this.goodsPrice
						},
						{
							date:tomorrow,
							money:this.num+"*"+this.goodsPrice
						}
					];
					this.stayTime = todayDate;	
				},
			//弹框弹出和取消事件
			togglePopup(type, open) {
				this.type = type;
				this.$refs[open].open();
			},	
			cancel(type) {
				this.$refs[type].close()
			},
			//弹框状态
			change(e) {
				console.log(e.show)
			},
			//点击入住时间修改页面的入住时间
			changeTime(e){
				this.time=e.currentTarget.dataset.date;
			},
			//修改数字按钮框动态修改钱数
			changeNumber(e){
				this.moneys=e*this.goodsPrice*this.dateSum;
				this.num = e;
			},
			//将子组件中底部提交事件暴露给父元素
			submit(){
				this.$emit('submit')
			},
			//将特产message5部分点击地址事件暴露给父元素
			goAddressPage(){
				this.$emit('goAddressPage')
			},
			//日历组件点击完成修改开始和结束时间
		    changeDate({ choiceDate, dayCount}) {
		    	this.beginDate = choiceDate[0].re;
		    	this.endDate = choiceDate[1].re;
		    	this.beginDateWeek = choiceDate[0].week;
		    	this.endDateWeek =  choiceDate[1].week;
		    	this.dateSum = dayCount; 
				
		    },
			//日历组件点击完成修改明细列表
			btn(stayArr){
				let myStayArr = stayArr.Popdetail;
				this.stayTime.length=0;
				console.log(myStayArr);
				console.log(this.stayTime);
				for (var i = 0; i < myStayArr.length-1; i++) {
					let obj = {
						date:myStayArr[i],
						money:this.num+"*"+this.goodsPrice
					}
					this.stayTime.push(obj);
				}
				 console.log(this.stayTime)
			},
			//特产页面选择配送方式点击事件
			bindClick(text,type,tagId){
				console.log("点击成功"+text+type+tagId);
				this.deliveryType = text;
				let tagIdNext = 0;
				if(tagId == 0){
					tagIdNext = 1;
				}else{
					tagIdNext = 0;
				}
				if(type == "default"){
					this.tag[tagId].tagType = "success";
					this.tag[tagIdNext].tagType = "default"
				}
			},
			//特产页面选择送货时间点击事件
			bindClick1(text,type,tagId){
				console.log("点击成功"+text+type+tagId);
				this.deliveryDate = text;
				let tagId1 = 0;
				let tagId2 = 0;
				if(tagId == 0){
					tagId1 = 1;
					tagId2 = 2;
				}else if(tagId == 1){
					tagId1 = 0;
					tagId2 = 2;
				}else{
					tagId1 = 0;
					tagId2 = 1;
				}
				if(type == "default"){
					this.tagNext[tagId].tagType = "success";
					this.tagNext[tagId1].tagType = "default"
					this.tagNext[tagId2].tagType = "default"
				}
			},
		    
		
		}
  
	}

